<template>
  <v-container fluid class="pa-0 pa-md-3">
    <v-list v-if="assets.length>0" class="ly-list">
      <template v-for="(asset, index) of assets">
        <v-list-item :key="asset[0]">
          <v-row no-gutters>
            <v-col cols="12" md="10" @click.stop="displayAsset(asset)">
              {{ asset[1] }}
            </v-col>
            <v-col cols="6" md="2" offset="6" offset-md="0" class="text-right">
              <v-btn icon @click.stop="onDeleteClick(asset, index)">
                <v-icon>mdi-delete-forever</v-icon>
              </v-btn>
              <v-btn icon @click.stop="onEditClick(asset, index)">
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-list-item>
        <v-divider :key="index" v-if="index!==(assets.length-1)"></v-divider>
      </template>
    </v-list>
    <v-row v-if="(assets.length==0) && pageReady" class="centered-row">
      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>
            Belum ada asset media!
          </v-card-title>
          <v-card-text>
            Tambahkan asset media dengan meng-klik tombol yang ada di sudut 
            kanan-bawah layar!
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-btn fab fixed bottom right dark color="pink" @click.stop="onFabAddClick">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <v-dialog v-model="dlgUpload" max-width="360" persistent>
      <v-card>
        <v-card-title>Upload Asset</v-card-title>
        <v-card-text>
          <v-row>
            <v-col cols="12">
              <v-file-input :label="fileSelectLabel" v-model="file"
                 accept=".mp4,.jpg,.png,.jpeg" hide-details :disabled="loading"
              >
              </v-file-input>
            </v-col>
            <v-col cols="12" v-show="loading">
              <v-progress-linear :value="percent"></v-progress-linear>
            </v-col>
            <v-col cols="12" v-show="loading" class="text-center">
              {{ infoText }}
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="warning" @click="onDlgUploadCancelClick">Batal</v-btn>
          <v-btn text color="primary" :disabled="null===file" @click="onBtnUploadClick" :loading="loading">Upload</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dlgEdit" max-width="360" persistent>
      <v-card>
        <v-card-title>Edit Asset</v-card-title>
        <v-card-text>
          <v-text-field label="Nama asset" v-model="editedText" clearable
            validate-on-blur :rules="[rules.required]"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dlgEdit=false">Batal</v-btn>
          <v-btn text color="primary" @click="onBtnConfirmEditClick" :loading="loading">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog max-width="320" v-model="dlgConfirmDel">
      <v-card>
        <v-card-title>Konfirmasi</v-card-title>
        <v-card-text>
          Hapus {{ assetNameToBeDel }}?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" :disabled="loading" @click="dlgConfirmDel=false">Batal</v-btn>
          <v-btn text color="warning" :disabled="loading" @click="deleteAsset">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="imageAssetPreview" max-width="640">
      <v-card>
        <v-img :src="imgAssetUrl" lazy-src="@/assets/l_small.png"></v-img>
      </v-card>
    </v-dialog>
    <v-dialog v-model="videoAssetPreview" max-width="640">
      <v-card>
        <video :src="videoAssetUrl" width="640" height="360" controls
          ref="videoPlayer"></video>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import S3 from 'aws-sdk/clients/s3'
import { API_URL, OLD_S3_SERVER } from '@/config'
import { deleteWithAuth, getWithAuth, postWithAuth } from '@/lib/fetch_utils'
import { fileExtToAssetType, getMediaFileExtension } from '@/lib/utils'
import { s3PreSignWCache } from '@/lib/aws_lib'
import { getMinioS3Resource } from '@/lib/s3_shim.js'

export default {
  name: "Assets",
  data: () => ({
    assetIndexToBeDel: -1,
    assetNameToBeDel: '',
    assets: [],
    dlgConfirmDel: false,
    dlgEdit: false,
    dlgUpload: false,
    editedIndex: -1,
    editedText: '',
    fileName: '',
    file: null,
    imageAssetPreview: false,
    imgAssetUrl: "",
    loading: false,
    pageReady: false,
    percent: 0,
    selectedAsset: null,
    videoAssetPreview: false,
    videoAssetUrl: "",
    rules: {
      required: function (v) {
        if (null === v) return "Harus diisi!"
        if (v.length < 2) return "Terlalu pendek!"
        return true
      }
    }
  }),
  computed: {
    infoText() {
      if (0 === this.percent) {
        return 'Initializing upload ...'
      } else if (100 === this.percent) {
        return '100% uploaded. Analyzing...'
      } else {
        return `${this.percent}% uploaded ...`
      }
    },
    fileSelectLabel() {
      if (null === this.file) {
        return "Klik di sini untuk memilih file!"
      } else {
        return "File terpilih"
      }
    },
  },
  watch: {
    loading: function() {
      this.$store.commit('SET_LOADING', this.loading)
    },
    videoAssetPreview: function() {
      if (!this.videoAssetPreview) {
        this.$refs.videoPlayer.pause()
      }
    },
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {title:"Assets"})
    this.loadData()
  },
  methods: {
    loadData: async function() {
      this.loading = true
      let resp = await getWithAuth(`${API_URL}/assets`)
      this.loading = false
      this.pageReady = true
      if (200 !== resp.code) return
      this.assets = resp.data.rows
    },
    onBtnUploadClick: async function() {
      if (OLD_S3_SERVER) {
        await this.uploadToOldS3Server()
      } else {
        await this.uploadToS3Server()
      }
    },
    encodeFilename(name) {
      let idx = name.lastIndexOf('.')
      if (-1 === idx) {
        return undefined
      }
      let base = btoa(name.substring(0, idx)).replace(/=/g, '')
      return base + name.substring(idx)
    },
    uploadToS3Server: async function() {
      let userId = this.$store.state.credentials.email
      let userIdB64 = btoa(userId).replace(/=/g, '')
      let fname = this.encodeFilename(this.file.name)
      if (undefined === fname) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: 'Invalid filename!'
        })
        return
      }
      let env = this.$store.state.stsCreds.env
      let s3 = new S3()
      this.percent = 0
      let params = {
        Bucket: env.s3_bucket,
        Key: `${env.application_id}/uploads/${userIdB64}/${fname}`,
        ContentType: this.file.type,
        Body: this.file
      }
      this.managedUpload = s3.upload(params)
      let self = this
      this.managedUpload.on('httpUploadProgress', progress => {
        self.percent = Math.round(100*progress.loaded/progress.total)
      })
      this.loading = true
      try {
        await this.managedUpload.promise()
      } catch(e) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: e
        })
        this.dlgUpload = false
        this.loading = false
        return
      }
      let resp = await postWithAuth(`${API_URL}/assets`, {fname: fname})
      this.loading = false
      this.dlgUpload = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      let asset = [
        resp.data,
        this.file.name,
        fileExtToAssetType(this.file.name),
        Math.round((new Date())/1000)
      ]
      this.assets.unshift(asset)
    },
    uploadToOldS3Server: async function() {
      let userId = this.$store.state.credentials.email
      let userIdB64 = btoa(userId).replace(/=/g, '')
      let fname = this.encodeFilename(this.file.name)
      if (undefined === fname) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: 'Invalid filename!'
        })
        return
      }
      let env = this.$store.state.stsCreds.env
      let s3 = getMinioS3Resource()
      // Check bucket existence! Create it if necessary!
      const bucket = 'cahkefao' // NOTE! This is special!
      try {
        let _resp = await s3.headBucket({ Bucket: bucket }).promise()
        if(200 !== _resp.$response.httpResponse.statusCode) {
          console.error(_resp.$response)
          return
        }
      } catch (error) {
        if (404 === error.statusCode) {
          try {
            let _r = await s3.createBucket({Bucket: bucket}).promise()
            console.debug(_r)
          } catch (err) {
            console.error(error)
            return
          }
        }
      }

      this.percent = 0
      let params = {
        Bucket: bucket,
        Key: `${env.application_id}/uploads/${userIdB64}/${fname}`,
        ContentType: this.file.type,
        Body: this.file
      }
      this.managedUpload = s3.upload(params)
      let self = this
      this.managedUpload.on('httpUploadProgress', progress => {
        self.percent = Math.round(100*progress.loaded/progress.total)
      })
      this.loading = true
      try {
        await this.managedUpload.promise()
      } catch(e) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: e
        })
        this.dlgUpload = false
        this.loading = false
        return
      }
      let resp = await postWithAuth(`${API_URL}/assets?primitive=1`, {fname: fname})
      this.loading = false
      this.dlgUpload = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      let asset = [
        resp.data,
        this.file.name,
        fileExtToAssetType(this.file.name),
        Math.round((new Date())/1000)
      ]
      this.assets.unshift(asset)
    },
    onEditClick(asset, index) {
      this.editedAssetId = asset[0]
      this.editedText = asset[1]
      this.editedIndex = index
      this.dlgEdit = true
    },
    presignAssetURL() {
      let userId = this.$store.state.credentials.email
      let assetId = this.selectedAsset[0]
      let ext = getMediaFileExtension(this.selectedAsset[2])
      let stsCreds = this.$store.state.stsCreds
      let env = stsCreds.env
      let key = `${env.application_id}/users/${userId}/assets/${assetId}.${ext}`
      let assetUrl = s3PreSignWCache(env.s3_bucket, key, 3600)
      return assetUrl
    },
    onBtnConfirmEditClick: async function() {
      if (this.editedText.length===0) return
      let asset = this.assets[this.editedIndex]
      let params = { name: this.editedText }
      this.loading = true
      let resp = await postWithAuth(`${API_URL}/assets/${asset[0]}`, params)
      this.loading = false
      this.dlgEdit = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      asset[1] = this.editedText
    },
    onFabAddClick() {
      this.file = null
      this.fileName = ''
      this.dlgUpload = true
    },
    onDlgUploadCancelClick() {
      if (this.loading) {
        this.managedUpload.abort()
        this.loading = false
      }
      this.dlgUpload = false
    },
    onDeleteClick (asset, index) {
      this.assetIndexToBeDel = index
      this.assetNameToBeDel = asset[1]
      this.dlgConfirmDel = true
    },
    deleteAsset: async function() {
      let asset = this.assets[this.assetIndexToBeDel]
      this.loading = true
      let resp = await deleteWithAuth(`${API_URL}/assets/${asset[0]}`)
      this.loading = false
      this.dlgConfirmDel = false
      if (200 !== resp.code) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: resp.message
        })
        return
      }
      this.assets.splice(this.assetIndexToBeDel, 1)
    },
    displayAsset(asset) {
      this.selectedAsset = asset
      switch (asset[2]) {
        case 0:
          this.videoAssetUrl = this.presignAssetURL()
          this.videoAssetPreview = true
          break
        case 1:
        case 2:
          this.imgAssetUrl = this.presignAssetURL()
          this.imageAssetPreview = true
          break
        default:
          break
      }
    }
  }
}
</script>

<style scoped>
.ly-list {
  margin-bottom: 64px;
}
</style>
